body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body, ul, li, ol, dl, dt, dd, figure, figcaption, a, p, h1, h2, h3, h4, h5, h6, input, form, button {
  padding: 0;
  margin: 0;
}
ul {
  list-style: none;
}

input {
  outline: none;
}

a {
  text-decoration: none;
}
a:link{text-decoration: none !important;}
a:visited{text-decoration: none !important;}
a:hover{text-decoration: none !important;}
a:active{text-decoration: none !important;} 

.container{
    width:100%;
    height:100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: absolute;
}
footer{
    height: 50px;
    width:100%;
}
footer ul {
    display: flex;
    width:100%;
}
footer ul li{
    flex: 1 1;
    line-height: 50px;
    text-align: center;
}
footer ul li a { color: black}
footer ul li a i {
    /* font-size: 13px; */
    margin: 5px;
    position: relative;
    top:0px;
}
header{
    height: 80px;
    background: #3c445e;
    width:100%;
    display: flex !important;
    justify-content: space-between;
}
.am-search{
   background: #3c445e;
   /* flex: 1; */
   width:50%;
   /* width:150px;
   float:right;  */
   top:18px;
}
.logo{
    width:100px;
    line-height: 80px;
    color:white;
    font-weight: bold;
}

.view{
    width:100%;
    flex:1 1;
    overflow: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    background: aliceblue;
}
.dynamicContainer .am-card-header-content img{
    width:30px;
    height: 30px;
}
.am-card-header-content{
    /* letter-spacing: 2px; */
}
.dynamicContainer .am-card-header .am-card-header-extra{
    width:100px !important;
    flex:none;
}
.dynamicContainer .am-card-header-content{
    color: rgb(125, 125, 125)
}
.dynamicContainer .am-textarea-control{
    border: 1px solid rgb(160, 160, 160);
    border-radius: 5px;
}
.dynamicContainer .am-list-item {padding: 0}
.repeatContainer div:nth-of-type(1){
    
}
.am-card-body{
    padding: 0 15px 6px;
}
.repeatList ul {
    max-height: 200px;
    overflow: hidden;
    overflow-y: auto;
    text-align: left;
    font-size: 14px;
    color: rgb(160, 160, 160);
    padding: 0 20px 20px 20px;
}
.repeatList ul .name{
    color: rgb(160, 160, 160);
    font-size: 13px;
}
.repeatList ul .name span{
    font-size: 14px;
}
.repeatList .context{
    text-indent: 2em;
    font-size: 14px;
}
.repeatList ul li {
    line-height: 24px;
}
.repeatList ul li{
    margin-top:10px;
    border-bottom: 1px solid #efeff4
}
.ql-editor {
    padding-top: 4px;
}
.dynamicTitle div:nth-of-type(2){
    font-size: 12px;
}
.dynamicOperationContainer{
    display: flex;
}
.dynamicOperationContainer div.dynamicDate{
    flex: 1 1;
    white-space: nowrap;
}
.dynamicOperationContainer div:nth-of-type(1){
    text-align: left;
    font-size: 12px;
}
.rightTemp{
    display: flex;
    width:100px;
}

.rightTemp div{
    flex: 1 1;
    text-align: center !important;
}
.rightTemp div:nth-of-type(1){
    margin-top: 2px;
}
.articleContainer {
    text-align: left;
}
.articleItem{
    max-height: 150px;
    overflow: hidden;
    border-radius: 10px;
    margin: 0 0 20px 0 ;
    padding: 20px;
    position: relative;
    color:rgb(102, 102, 102);
    /* border: 1px solid #ccc; */
    font-size: 12px;
    background: white;
}
.articleItem .ql-editor {
    overflow: hidden;
}
.articleItem h2, .articleItem h2 span{
    font-size: 15px;
    color:rgb(102, 102, 102) !important;
}
/* .operation{
    position: absolute;
    right:20px;
    top:20px;
    font-size: 23px;
} */
.articleStatus{
    display: flex;
    position: relative;
    top:-7px;
}
.articleStatus div{
    flex: 1 1;
    text-align: center;
}
.articleStatus div:nth-of-type(1){
    text-align: left;
}
.articleStatus .operation div{
    text-align: right;
}
.detailContainer{
    text-align: left;
}
.detailContainer img{
    max-width: 300px;
    display: block;
    margin: 0 auto;
}
.detailContainer h2 , .detailContainer h2 span{
    font-weight: normal;
    text-align: center;
    color:rgb(102, 102, 102) !important
}
pre{
    white-space: pre-wrap;   
    background-color: #23241f !important;
    color: #f8f8f2 !important;
    overflow: visible !important;
    white-space: pre-wrap;
    padding: 20px !important;
}
.articleStatus1{
    display: flex;
    margin-top:15px;
}
.articleStatus1 div{
    flex: 1 1;
    text-align: center;
    line-height: 19px;
}
.messageContainer{
    width:100%;
    height: 100%;
}
.messageItem .am-card-header-content {
    color: rgb(102, 102, 102);
    letter-spacing: 0;
}
.messageItem .am-card-header-content img {
    width:40px;
    height:40px;
    border-radius: 20px;
}
.messageOperationContainer{
    width:70px;
    display: flex;
    float: right;
    justify-content: space-between;
}
.messageOperationContainer div:nth-of-type(1){
    width:50%;
    text-align: center;
}
.messageOperationContainer div:nth-of-type(2){
    width:50%;
    text-align: center;
}
.messageContainer .messageDate {
    font-size: 12px;
    letter-spacing: 0;
    padding-left: 7px;
    line-height: 15px;
    color: rgb(125, 125, 125);
}
.repeatItemContainer{
    background: #ccc;
    text-align: left;
    color: rgb(125, 125, 125);
    font-size: 12px;
    padding-left: 61px;
    position: relative;
    -webkit-overflow-scrolling: touch;
}
.dot{
    position: absolute;
    top: 0px;
    height: 20px;
    width: 20px;
    right: 46px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    background:#ccc;
}
.repeatItemContainer div{
    border: none !important;
    padding: 4px;
}
.icon{
    transition:all .3s;
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
.closeIcon{
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg)
}
div.open{
    height: auto;
}
.sendMsg{
    position: fixed;
    /* left: 50%; */
    right: -23px;
    /* margin-left: -25px; */
    /* bottom: 60px; */
    margin-top: -25px;
    top: 50%;
    background: #ccc;
    z-index: 1;
    font-size: 20px;
    color: #444;
    width: 50px;
    line-height: 50px;
    padding-right: 14px;
    height: 50px;
    border-radius: 25px;
    /* background: #3c445e; */
}
.sendMsg .sendMsgI {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
        transition: all .4s;
}
i.sengMsgRotate{
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.loginContainer{
    width:100%;
    height:100%;
    transition: all .4s;
    background: #ccc;
    position: absolute;
    z-index: 10;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    will-change: transform;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    top:0;
    left:0;
    /* overflow-y: auto; */
    /* -webkit-overflow-scrolling: touch; */
}
.loginRegContainer{
    width:100%;
    height: 100%;
    bottom:0;
    left:0;
    background: url(data:image/png;base64,) repeat-x;
    transition: all .3s;
    z-index: 11;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    position: absolute;

    overflow: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}
.loginLogo{
    color:white;
    font-weight: bold;
}
.loginLogo .loginLogoDiv:nth-of-type(1){
    width:50px;
    height: 50px;
    display: block;
    line-height: 54px;
    text-align: center;
    border-radius: 25px;
    background: #1890ff;
    margin: 0 auto;
}
.loginLogo .loginLogoDiv:nth-of-type(2){
color: white;
}
.loginLogo i {
    font-size: 20px;
}
.backIcon{
    width:50px;
    height:50px;
    margin:20px;
    text-align: center;
    line-height: 50px;
    color:white;
    margin-left: -10px;
}
.showContainer{
    -webkit-transform: translateX(0);
            transform: translateX(0)
}
.asideToggle{
     -webkit-transform: translateX(-40%);
             transform: translateX(-40%);
}
.entrySwitch{
    position: absolute;
    top:50%;
    right:-25px;
    width:50px;
    height: 50px;
    border-radius: 25px;
    background: #ccc;
    padding-left: 20px;
    margin-top: -25px;
}
.entrySwitch i {
    font-size: 19px;
    line-height: 57px;
    transition: all .6s;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
.entrySwitch i.toggle{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    line-height: 47px;
}
.leftContainer{
    width:100%;
    height:80%;
    flex-direction: column;
    display: flex;
    margin: 10% auto;
}
.avatarContainer{
    width:100%;
    height:130px;
    position: relative;
}
.avatarContainer img{
    width:100px;
    height: 100px;
    margin: 0 auto;
    border: 1px solid white;
    border-radius: 50px;
    display: block;
    position: absolute;
    left:70%;
    margin-left: -50px;
}
.upload{
    width:100px;
    height: 100px;
    margin: 0 auto;
    border: 1px solid white;
    border-radius: 50px;
    display: block;
    overflow: hidden;
    position: absolute;
    z-index: 1;
    left:70%;
    opacity: 0;
    margin-left: -50px;
}
.upload span, .upload div, .upload input {
    width:100%;
    height:100%;
    display: block !important;
}
.userName{
    display: block;
    position: absolute;
    left:70%;
    width:100px;
    text-align: center;
    color:#504e4e;
    bottom:0;
    margin-left: -50px;
}
.operationListContainer{
    position: relative;
flex: 1 1;
}
.operationList{
    width:200px;
    text-align: left;
    margin-left: 40%;
    position: absolute;
    top:50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    color:#504e4e;
}
.operationItem{
    height: 40px;
    line-height: 40px;
    padding-left: 30px;
}
.operationItem i {
    position: relative;
    top:2px;
    right:3px;
}
.loginRegContainer .ant-checkbox-wrapper{
    float: left;
}
#components-form-demo-normal-login .login-form {
  max-width: 300px;
}
#components-form-demo-normal-login .login-form-forgot {
  float: right;
}
.loginRegContainer .login-form-button {
  width: 100%;
}
.loginRegContainer .ant-form-item{
    margin-bottom: 10px !important;
    height: 50px !important; 
}
.loginRegContainer ._input{
    height: 40px !important;
    display: block;
}
.loginRegContainer .ant-form-item-control-wrapper, .ant-form-item-control, .ant-form-item-children{
    height: 42px;
    display: block;
}
